<template>
  <div>
    <div class="lantern-container">
      <div class="lantern-line"></div>
      <div class="lantern-body">
        <div class="outer">
          <div class="inner"></div>
        </div>
        <div class="tassel">
          <div class="line"></div>
          <div class="circle"></div>
          <div class="rect"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'lantern'
}
</script>

<style>
/* .lantern-container {
  position: fixed;
} */
.lantern-line {
  width: 2px;
  height: 45px;
  margin: 0 auto;
  background-color: #dc8f03;
}
.lantern-body {
  position: relative;
  width: 120px;
  height: 90px;
  margin: 0 auto;
  background-color: red;
  border-radius: 50%;
  box-shadow: -5px 5px 50px 4px #fa6c00;
  animation: swing 3s infinite ease-in-out;
  transform-origin: top center;
}
@keyframes swing {
  0% {
    transform: rotate(-6deg);
  }

  50% {
    transform: rotate(6deg);
  }

  100% {
    transform: rotate(-6deg);
  }
}
.lantern-body::before,
.lantern-body::after {
  content: '';
  position: absolute;
  border: 1px solid #dc8f03;
  width: 60px;
  height: 12px;
  background: linear-gradient(
    to right,
    #dc8f03,
    #ffa500,
    #dc8f03,
    #ffa500,
    #dc8f03
  );
  left: 30px;
}
.lantern-body::before {
  top: -7px;
  border-radius: 5px 5px 0 0;
}
.lantern-body::after {
  bottom: -7px;
  border-radius: 0 0 5px 5px;
}
.outer,
.inner {
  height: 90px;
  border-radius: 50%;
  border: 2px solid #dc8f03;
  margin: -4.5px auto 0 auto;
  background-color: rgba(216, 0, 15, 0.1);
}
.outer {
  width: 100px;
}
.inner {
  width: 45px;
}
.tassel {
  animation: swing 3s infinite ease-in-out;
}
.tassel .line {
  height: 20px;
  width: 5px;
  margin: 0 auto;
  background-color: #ffa500;
  border-radius: 0 0 5px 5px;
  z-index: -1;
}
.circle {
  position: relative;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #dc8f03;
  margin: -7px auto 0 auto;
  z-index: 1;
}
.rect {
  width: 10px;
  height: 20px;
  border-radius: 4px 4px 0 4px;
  background-color: #ffa500;
  margin: -10px auto 0 auto;
  z-index: -1;
}
</style>